/* SECTION BANNER */
.banner{
    display: flex;
    justify-content:center;
    align-items:center;
    height: 50rem;
    padding: 2rem;
    background: url("../img/mountains.png") center/cover no-repeat;
}

.intro{
    display: flex;
    align-items:center;
    justify-content:center;
    text-align: center;
    overflow: hidden;
    transform: translateY(-1%);
}

.intro h1{
    font-size:2rem;
}

            /* Animation for "I'm Juan Garcia..." */
.anim-typewriter{
    animation: typewriter 3s steps(44) .5s 1 normal both,
               blinkTextCursor 500ms steps(44) infinite normal;
  }
  @keyframes typewriter{
    from{width: 0;}
    to{width: 30em;}
  }
  @keyframes blinkTextCursor{
    from{border-right-color: rgba(255,255,255,.75);}
    to{border-right-color: transparent;}
  }  

  

  /* SECTION SKILLS */

section.skills{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  margin: 0;
  padding:3rem;
  background-color: lightgray;
}

.skills_bar{
  justify-content: center;
  width: 75%;
  padding: 2rem;
}

.container h1{
  align-items: flex-start;
  padding-top: 1rem;
  }

  /* Animation for "Skills bar..." */
@keyframes load{
    from {
      width: 0%
    }
  }
@-webkit-keyframes load{
    from {
      width: 0%
    }
  }
@-moz-keyframes load{
    from {
      width: 0%
    }
  }
@-o-keyframes load{
    from {
      width: 0%
    }
  }
  
.bar{
  padding: 2px;
  border-radius: 25px;
  margin-bottom: 5px;
  background-color: #EEE;
  font-size: 24px;
  color: #FFF;
  font-weight: bold;
  text-shadow: 1px 1px 1px rgba(0,0,0,0.5);
  }
.bar::before{
    content:  attr(skill);
    background-color: darkblue;
    display: inline-block;
    padding: 5px 0 5px 10px;
    border-radius: inherit;
    animation: load 2s 0s;
    -webkit-animation: load 2s 0s;
    -moz-animation: load 2s 0s;
    -o-animation: load 2s 0s;
  }
  
  .bar.front::before{
    background-color: #ffcc33;
  }
.bar.back::before{
    background-color: #a6cfe3;
  }
.bar.bar.novice::before::before{
    width: calc(40% - 10px);
    padding: 3rem;
  }
.bar.intermediate::before{
    width: calc(60% - 10px);
  }
.bar.proficient::before{
    width: calc(80% - 10px);
  }
.bar.expert::before{
    width: calc(100% - 10px);
  }

